import styled from "styled-components";
export const CopyButtonStyled = styled.div`
  position: relative;
  color: #4de791;
  > span {
    position: absolute;
    top: -25px;
    left: -5px;
  }
  > .ant-btn {
    border: 0;
    > i {
      font-size: 20px;
    }
  }

  > .ant-btn:focus,
  .ant-btn:active {
    color: #4de791;
  }
`;
