import EndPoint from "components/molecules/EndPoint";
import { Grid } from "mylibs/util-sc";
import { IApiReference } from "types/Interface";

interface Props {
  api_reference: IApiReference;
  cargando?: boolean;
}

const EndpointList = ({ api_reference, cargando = true }: Props) => {
  return (
    <Grid gap="10px" cols={2} responsive={true} colsWidthResponsive="100%">
      {cargando ? (
        <span style={{ color: "white" }}>Cargando referencias...</span>
      ) : api_reference ? (
        api_reference.routing.map((el, key) => <EndPoint data={el} key={key} />)
      ) : (
        <span style={{ color: "white" }}>
          No hay referencias disponibles...
        </span>
      )}
    </Grid>
  );
};

export default EndpointList;
