import axios, { AxiosInstance } from "axios";
import axiosTokenInterceptor from "axios-token-interceptor";
import { baseURL } from "index";
import { useEffect, useState } from "react";
import { useUsuario } from "./globalUser";

type T_Data = any | any[] | undefined;
type T_METHOD_HTTP = "POST" | "GET" | "UPDATE" | "DELETE" | "INSERT";

export const useAxios = (
  METHOD: T_METHOD_HTTP,
  URL: string,
  autoInit: boolean = true,
  params?: object
) => {
  const { usuario } = useUsuario()
  const [data, setData] = useState<T_Data>(undefined);
  const [error, setError] = useState(null);
  const [responseServerCode, setResponseServerCode] = useState<number>();
  const [loading, setLoading] = useState<boolean>(false);

  const updateParams = (newParams : any) => {
    params = newParams
  }

  const resetValues = () => {
      setData(undefined)
      setError(null)
      setLoading(false)
      setResponseServerCode(undefined)
  }

  const initAxiosInstance = (): AxiosInstance => {
    const instance = axios.create({ baseURL });
    instance.interceptors.request.use(
      axiosTokenInterceptor({ getToken: () => `${usuario ? usuario.token : "NO TENGO TOKEN"} ${usuario ? usuario.idrol : 0}` })
    );
    return instance;
  };

  const sendRequest = async (instance: AxiosInstance) =>
    await instance.post(URL, METHOD === "GET" ? null : params);

  const initRequest = async () => {
    setLoading(true);
    try {
      const res = await sendRequest(initAxiosInstance());
      setData(res.data)
      setResponseServerCode(res.status)
      setLoading(false);
    } catch (error) {
      setResponseServerCode(500)
      setError(error);
      setLoading(false);
    }
  };

  const clientInitRequest = (isReload : boolean = false) => {
      !isReload && resetValues();
      initRequest()
  }

  useEffect(() => {
    autoInit && clientInitRequest()
    return () => {
      
    }
  }, [usuario, autoInit])


  return { data, loading, responseServerCode, error, updateParams, clientInitRequest };
};
