import EndpointList from "components/organisms/EndpointList"
import { useAxios } from "hook/useAxios"

const Modulo = () => {
    const api_modulo = useAxios("POST","/api/references/modulo")
    return (
        <div>
            <EndpointList api_reference = {api_modulo.data} cargando={api_modulo.loading}/>
        </div>
    )
}

export default Modulo
