import { IParams } from "types/Interface";

export const TransformarParametrosAInterface = (params: IParams[],name: string,type: "INPUT" | "OUTPUT") => {
  return `export interface DEF_${name.replaceAll(/([a-z])([A-Z])/g,"$1_$2").toUpperCase()}_${type} {${params.map(
    (el) => `${el.name}${!el.required ? "?" : ""} : ${el.type}`
  )}}`;
};
