import CopyButton from "components/atoms/CopyButton";
import { RutaStyled } from "./styled"
interface Props {
    method : "POST" | "GET" | "UPDATE" | "HEAD" | "DELETE"
    url : string,
    name: string
}

const Ruta = ( { method , url, name } : Props ) => {

    return (
        <RutaStyled>
            <span>
                {method}
            </span>
            <span>
                {url}
            </span>
            <CopyButton value={`export const API_${name} = "${url}"`} />
        </RutaStyled>
    )
}

export default Ruta
