import { Button } from "antd";
import useCopyClipboard from "react-use-clipboard";
import { CopyButtonStyled } from "./styled";

interface Props {
    value : string 
}

const CopyButton = ( { value } : Props) => {
    const [ isCopied, setCopied ] = useCopyClipboard(value, {
        successDuration: 1000,
      });

    return (
        <CopyButtonStyled onClick={setCopied}>
            <span>
                { isCopied && "Â¡Copiado!"}
            </span>
            <Button type="primary">
                <i className="fa fa-copy"></i>
            </Button>
        </CopyButtonStyled>
    )
}

export default CopyButton
