import { Card } from "antd"
import { Item } from "components/atoms"
import { Column } from "mylibs/util-sc"
import { IRoutingConfig } from "types/Interface"
import { Controlador, Ruta } from ".."
import { EndPointStyled } from "./styled"

const EndPoint = ({data} : {data : IRoutingConfig}) => {
    return (
        <EndPointStyled hoverable bordered>
            <Column gap="10px">
                <Item label="REFERENCIA:">
                    API_{data.controller.name.toUpperCase()}
                </Item>
                <Ruta method={data.method} url={data.url} name={data.controller.name.toUpperCase()}/>
                <Controlador data={data.controller}/>
            </Column>
        </EndPointStyled>
    )
}

export default EndPoint
