import { Item } from "components/atoms";
import CopyButton from "components/atoms/CopyButton";
import { DataTable } from "components/organisms";
import { TransformarParametrosAInterface } from "functions/lib";
import { Fragment } from "react";
import { IParams } from "types/Interface";

interface Props {
  interfaceTS?: string;
  params: IParams[];
  title: string;
  name: string;
  type: "INPUT" | "OUTPUT"
}

const Params = ({ interfaceTS, params, title, name, type }: Props) => {
  return (
    <Item
      icon="angle-double-right"
      label={title}
      extra={<CopyButton value={TransformarParametrosAInterface(params,name,type)} />}
    >
      <Item icon="" label="- Interface TS">
        {`DEF_${name.replaceAll(/([a-z])([A-Z])/g,"$1_$2").toUpperCase()}_${type}`}
      </Item>
      <Fragment>
        {params.length > 0 && (
          <DataTable
            columnSource={["name", "type", "required"]}
            dataSource={params}
          />
        )}
      </Fragment>
    </Item>
  );
};

export default Params;
