
import { Item } from "components/atoms";
import { IController } from "types/Interface";
import { Params } from "..";
interface Props {
  data: IController;
}

const Controladores = ({ data }: Props) => {
  return (
    <div>
      <Item icon="database" label="Procedimiento Almacenado">
        {data.procedureStore}
      </Item>
      {data.inputParams && (
        <Params
          title="Parametros de entrada"
          name= {data.name}
          type="INPUT"
          interfaceTS={data.inputParams.interfaceTS}
          params={data.inputParams.params}
        />
      )}
      {data.outputParams && (
        <Params
          title="Parametros de salida"
          name= {data.name}
          type="OUTPUT"
          interfaceTS={data.outputParams.interfaceTS}
          params={data.outputParams.params}
        />
      )}
    </div>
  );
};
export default Controladores;
